// TODO: build this from tokenData
const tokenAddrToSymbol = {
  "0x583cbBb8a8443B38aBcC0c956beCe47340ea1367": "BOKKY",
  "0x0000000000000000000000000000000000000000": "ETH"
};

const styles = theme => ({
  root: {
    width: "100%"
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: "33.33%",
    flexShrink: 0
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary
  },
  button: {
    margin: theme.spacing.unit
  },
  input: {
    display: "none"
  }
});

const sleep = milliseconds => {
  return new Promise(resolve => setTimeout(resolve, milliseconds));
};

const PaymentStatus = { OPEN: 0, CLOSED: 1, CANCELLED: 2 };
class PaymentExpansionPanel extends React.Component {
  constructor(props) {
    super(props);
    /*
                createTransaction={this.createTransaction}
        retrieveFunds={this.retrieveFunds}
        cancelTransaction={this.cancelTransaction}
        getTransactions={this.getTransactions}
         */
    this.state = {
      openNew: false,
      newTransaction: {
        id: "",
        recipient: "",
        amount: "",
        holdTime: { days: 0, hours: 0, minutes: 0, seconds: 0 }
      }
    };
  }

  render() {
    const { classes } = this.props;

    // Custom comparator to sort transactions with the most
    // recent at the top.
    function latestTsFirst(a, b) {
      if (a.transactionOpenTime > b.transactionOpenTime) return -1;
      if (b.transactionOpenTime > a.transactionOpenTime) return 1;

      return 0;
    }

    let sortedTransactions = this.props.allTransactions.sort(latestTsFirst);
    let openTransactions = [];
    let closedTransactions = [];

    // TODO: should I be making a copy here?
    for (let i = 0; i < sortedTransactions.length; i++) {
      if (sortedTransactions[i].state === 0) {
        openTransactions.push(sortedTransactions[i]);
      } else {
        closedTransactions.push(sortedTransactions[i]);
      }
    }

    return (
      <div className={classes.root}>
        <CssBaseline />
        <div style={{ marginLeft: 25 }}>
          <Typography variant="h5" component="h3">
            My Open Payments
          </Typography>
        </div>
        <br />
        {openTransactions.length === 0 ? (
          <div style={{ marginLeft: 25 }}>
            <Typography variant={"h6"} component={"h3"}>
              No open payments in this date range.
            </Typography>
          </div>
        ) : (
          openTransactions.map((item, index) => {
            return (
              <TransactionPanel
                _key={index}
                key={item.paymentId}
                classes={this.props.classes}
                transaction={item}
                toggleWithdraw={this.props.toggleMenuOpen("openTx")("withdraw")}
                toggleCancel={this.props.toggleMenuOpen("openTx")("cancel")}
                openWithdraw={
                  this.props.menuOpen["openTx"]["withdraw"] === index
                }
                openCancel={this.props.menuOpen["openTx"]["cancel"] === index}
                retrieveFunds={this.props.retrieveFunds}
                retrieveFundsFeeless={this.props.retrieveFundsFeeless}
                cancelTransaction={this.props.cancelTransaction}
                allAccounts={this.props.allAccounts}
                uniqueKey={"open"}
                getEtherScanURL={this.props.getEtherScanURL}
              />
            );
          })
        )}
        <br />
        <div style={{ marginLeft: 25 }}>
          <Typography variant="h5" component="h3">
            My Closed Payments
          </Typography>
        </div>
        <br />
        {closedTransactions.length === 0 ? (
          <div style={{ marginLeft: 25 }}>
            <Typography variant={"h6"} component={"h3"}>
              No closed payments in this date range.
            </Typography>
          </div>
        ) : (
          closedTransactions.map((item, index) => {
            return (
              <TransactionPanel
                _key={index}
                key={index}
                classes={this.props.classes}
                transaction={item}
                toggleWithdraw={this.props.toggleMenuOpen("closedTx")(
                  "withdraw"
                )}
                toggleCancel={this.props.toggleMenuOpen("closedTx")("cancel")}
                openWithdraw={
                  this.props.menuOpen["closedTx"]["withdraw"] === index
                }
                openCancel={this.props.menuOpen["closedTx"]["cancel"] === index}
                retrieveFunds={this.props.retrieveFunds}
                retrieveFundsFeeless={this.props.retrieveFundsFeeless}
                cancelTransaction={this.props.cancelTransaction}
                allAccounts={this.props.allAccounts}
                uniqueKey={"closed"}
                getEtherScanURL={this.props.getEtherScanURL}
              />
            );
          })
        )}
      </div>
    );
  }
}

function addrEquals(str1, str2) {
  try {
    return str1.toLowerCase() === str2.toLowerCase();
  } catch (err) {
    return str1 === str2;
  }
}

class TransactionPanel extends React.Component {
  constructor(props) {
    super(props);
  }

  static PaymentStatusToName = {
    0: "OPEN",
    1: "COMPLETED",
    2: "CANCELLED"
  };

  render() {
    const classes = this.props.classes;
    const account =
      this.props.allAccounts === undefined || this.props.allAccounts === []
        ? 0
        : this.props.allAccounts[0];

    const txn = this.props.transaction;

    const openDate =
      txn.transactionOpenTime === 0
        ? undefined
        : new Date(txn.transactionOpenTime * 1000);
    const closeDate =
      txn.transactionCloseTime === 0 || txn.transactionCloseTime === undefined
        ? undefined
        : new Date(txn.transactionCloseTime * 1000);

    const shortAddr = (string, num) => {
      return string.substring(0, num) + "...";
    };

    // TODO: figure out what to do with self transactions
    const fromOrTo = (() => {
      if (addrEquals(account, txn.recipient)) {
        if (addrEquals(account, txn.sender)) {
          return "SELF TRANSACTION";
        } else {
          return "FROM: " + shortAddr(txn.sender, 12);
        }
      } else {
        return "TO: " + shortAddr(txn.recipient, 12);
      }
    })();

    const status = txn.state;

    // TODO: should be latest block TS.
    const currTs = Date.now();

    const renderRecipientActions = (txn, timeLeft) => {
      return (
        <React.Fragment>
          <Button
            className={classes.button}
            variant="contained"
            color="primary"
            disabled={status !== PaymentStatus.OPEN || timeLeft === 0}
            size="small"
            onClick={this.props.toggleWithdraw(this.props._key)}
          >
            Withdraw Funds
          </Button>
          <WithdrawDialog
            _key={this.props._key}
            toggleWithdraw={this.props.toggleWithdraw}
            open={this.props.openWithdraw}
            retrieveFunds={this.props.retrieveFunds}
            retrieveFundsFeeless={this.props.retrieveFundsFeeless}
            transaction={txn}
          />
        </React.Fragment>
      );
    };

    const renderSenderActions = (txn, timeLeft) => {
      return (
        <React.Fragment>
          <Button
            className={classes.button}
            variant="contained"
            color="secondary"
            disabled={status !== PaymentStatus.OPEN}
            size="small"
            onClick={this.props.toggleCancel(this.props._key)}
          >
            Cancel Transaction
          </Button>
          <CancelTransactionDialog
            _key={this.props._key}
            toggleCancel={this.props.toggleCancel}
            open={this.props.openCancel}
            cancelTransaction={this.props.cancelTransaction}
            transaction={txn}
          />
        </React.Fragment>
      );
    };

    // TODO: defined twice.
    const formatTimeJSON = jsonStr => {
      return (
        "Days: " +
        jsonStr["days"] +
        ", Hours: " +
        jsonStr["hours"] +
        ", Minutes: " +
        jsonStr["minutes"]
      );
    };

    let utils = window.web3.utils;

    let symbol = tokenAddrToSymbol[txn.token];
    let numDecimals = 18;
    if (symbol !== "ETH") {
      numDecimals = parseInt(App.ropstenTokenMap[symbol].decimals);
    }

    let amount =
      symbol === "ETH"
        ? utils.fromWei(txn.sendAmount.toString(), "ether")
        : txn.sendAmount / Math.pow(10, numDecimals);

    // TODO: need to use block timestamp here, unless accuracy +/- 15 min is not needed.
    let timeLeft =
      txn.transactionOpenTime * 1000 + txn.holdTime * 1000 > currTs
        ? 1000 * txn.transactionOpenTime + 1000 * txn.holdTime - currTs
        : 0;

    return (
      <ExpansionPanel>
        <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
          <Typography className={classes.heading}>
            {openDate.toLocaleString().split(",")[0]}
            <br />
            {openDate.toLocaleString().split(",")[1]}
          </Typography>
          <Typography className={classes.heading}>{fromOrTo}</Typography>
          <Typography className={classes.heading}>
            {TransactionPanel.PaymentStatusToName[status]}
          </Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <div>
            <Typography>
              <strong>Payment ID:</strong>{" "}
              {shortAddr(window.web3.utils.toBN(txn.paymentId).toString(), 12)}
              <br />
              <strong>From:</strong> {txn.sender}
              <br />
              <strong>To:</strong> {txn.recipient}
              <br />
              <strong> Token:</strong> {symbol}
              <br />
              <strong> Amount:</strong> {amount}
              <br />
              <strong>Time opened:</strong> {openDate.toLocaleString()}
              <br />
              <strong>Time closed:</strong>{" "}
              {closeDate === undefined ? "N/A" : closeDate.toLocaleString()}
              <br />
              <strong> Holding period:</strong>{" "}
              {formatTimeJSON(millisToJSON(txn.holdTime * 1000))}
              <br />
              <strong> Time left:</strong>{" "}
              {formatTimeJSON(millisToJSON(timeLeft))}
              <br />
            </Typography>
            <br />
            <Typography variant={"h6"} component={"h4"}>
              {" "}
              Transactions:{" "}
            </Typography>
            <br />
            <div>
              <strong>Payment opened:</strong>
              <IconButton
                href={this.props.getEtherScanURL(txn.openHash)}
                target={"_blank"}
              >
                <OpenInNew />
              </IconButton>
              <TextField
                id={
                  "outlined-multiline-static-hash-0-" +
                  this.props.uniqueKey +
                  "-" +
                  this.props._key
                }
                value={txn.openHash}
                disabled={true}
                margin="normal"
                label={"Transaction hash"}
                variant="outlined"
                fullWidth
              />
            </div>
            {status === PaymentStatus.OPEN ? (
              ""
            ) : (
              <div>
                <strong>Payment closed:</strong>
                <IconButton
                  href={this.props.getEtherScanURL(txn.closeHash)}
                  target={"_blank"}
                >
                  <OpenInNew />
                </IconButton>
                <TextField
                  id={
                    "outlined-multiline-static-hash-1-" +
                    this.props.uniqueKey +
                    "-" +
                    this.props._key
                  }
                  value={txn.closeHash}
                  disabled={true}
                  margin="normal"
                  label={"Transaction hash"}
                  variant="outlined"
                  fullWidth
                />
              </div>
            )}
            <br />
            {status !== PaymentStatus.OPEN ? (
              <React.Fragment>
                <br />
                This transaction is closed.
              </React.Fragment>
            ) : (
              ""
            )}
            {timeLeft === 0 ? (
              <React.Fragment>
                <br />
                The time window to complete payment has closed.
              </React.Fragment>
            ) : (
              ""
            )}
          </div>
        </ExpansionPanelDetails>
        <Divider />
        <ExpansionPanelActions>
          {addrEquals(account, txn.recipient)
            ? renderRecipientActions(txn, timeLeft)
            : ""}
          {addrEquals(account, txn.sender)
            ? renderSenderActions(txn, timeLeft)
            : ""}
        </ExpansionPanelActions>
      </ExpansionPanel>
    );
  }
}

function millisToJSON(ms) {
  let d, h, m, s;
  s = Math.floor(ms / 1000);
  m = Math.floor(s / 60);
  s = s % 60;
  h = Math.floor(m / 60);
  m = m % 60;
  d = Math.floor(h / 24);
  h = h % 24;
  return { days: d, hours: h, minutes: m, seconds: s };
}

PaymentExpansionPanel.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(PaymentExpansionPanel);
