import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import Divider from "@material-ui/core/Divider";
import TextField from "@material-ui/core/TextField";
import SubmitDialog from "./SubmitDialog";

import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import FileCopyIcon from "@material-ui/icons/FileCopy";
import RefreshIcon from "@material-ui/icons/Refresh";
import IconButton from "@material-ui/core/IconButton";
import InputAdornment from "@material-ui/core/InputAdornment";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";

import { CopyToClipboard } from "react-copy-to-clipboard";
import Tooltip from "@material-ui/core/Tooltip";
import Switch from "@material-ui/core/Switch";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import App from "./App";

const BigNumber = require("bignumber.js");
const niceware = require("niceware");

const styles = theme => ({
  root: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit
  },
  button: {
    margin: theme.spacing.unit
  },
  input: {
    display: "none"
  },
  formControl: {
    margin: theme.spacing.unit,
    minWidth: 120
  },
  selectEmpty: {
    marginTop: theme.spacing.unit * 2
  }
});

const toBN = amt => {
  return new BigNumber(amt);
};

// TODO: refactor into createTransaction() and createTokenTransaction().
function toWeiBN(amount, denom) {
  let sendAmount = toBN(
    // Assumes amount is valid.
    window.web3.utils.toWei(toBN(amount).toFixed(), denom)
  );

  return sendAmount;
}
// TODO: refactor into createTransaction() and createTokenTransaction().
function toTokenBN(amount, denom) {
  let tokenInfo = App.ropstenTokenMap[denom];
  console.log("Amount is: " + amount);
  // Assumes amount is valid.
  return toBN(amount).times(toBN("10").pow(tokenInfo.decimals));
}

class NewTransaction extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount = () => {
    this.updateBalance(true);
  };

  state = {
    code: niceware.generatePassphrase(6),
    submitOpen: false,
    denom: "wei",
    copied: false,
    showPassword: false,
    sendEther: true,
    //token: 'DAI',
    token: "BOKKY",
    displayBalance: "0",
    realBalance: 0,
    balanceLoaded: false,
    balanceEnough: false
  };

  handleClickShowPassword = () => {
    this.setState(state => ({ showPassword: !state.showPassword }));
  };

  refreshCode = event => {
    this.setState({ code: niceware.generatePassphrase(6), copied: false });
  };

  toggleSubmitDialog = bool => event => {
    this.setState({ submitOpen: bool });
  };

  handleSelectChange = event => {
    this.setState({ [event.target.name]: event.target.value });
    // Update balance if we switch to a new token/ denom
    if (event.target.name === "token") {
      this.updateBalance(false);
    } else if (event.target.name === "denom") {
      this.updateBalance(true);
    }
  };

  //  https://web3js.readthedocs.io/en/1.0/web3-utils.html#towei
  static denomToPrecision = {
    wei: 1,
    Kwei: 3,
    babbage: 3,
    Mwei: 6,
    lovelace: 6,
    Gwei: 9,
    shannon: 9,
    microether: 12,
    szabo: 12,
    milliether: 15,
    finney: 15,
    ether: 18
  };

  static whitelistedTokens = ["BOKKY"];
  /*
        [
        'DAI',
        'BAT',
        'OMG',
        'WTC',
        'BNB',
        'MKR',
        'ZRX',
        'ENJ',
        'LINK',
         'PPT',
    ];
    */

  static selectedDenoms = [
    "wei",
    "Kwei",
    "Mwei",
    "Gwei",
    "microether",
    "milliether",
    "ether"
  ];

  isBadTimeInput = input => {
    return (
      this.props.newTransaction.holdTime[input].toString() !==
      parseInt(this.props.newTransaction.holdTime[input]).toString()
    );
  };

  handleSwitch = async event => {
    this.updateBalance(!this.state.sendEther);
    this.setState({ sendEther: !this.state.sendEther });
  };

  isBalanceEnough = (balance, amount) => {
    if (!this.isValidAmount(amount)) {
      return false;
    } else {
      let denom = this.state.sendEther ? this.state.denom : this.state.token;
      let rawAmount = this.state.sendEther
        ? toWeiBN(amount, denom)
        : toTokenBN(amount, denom);
      return !toBN(balance)
        .minus(rawAmount)
        .isNegative();
    }
  };

  updateIsBalanceEnough = (balance, amount) => {
    let isBalanceEnough = this.isBalanceEnough(balance, amount);
    this.setState({ balanceEnough: isBalanceEnough });
  };

  async updateBalance(useEth) {
    this.setState({ balanceLoaded: false });
    let balance;

    // TODO: use a function to display the realBalance instead of
    // using two vars.
    let balanceEnough;
    let realBalance;

    if (useEth) {
      realBalance = await this.props.updateTokenBalance("ETH");
      balanceEnough = this.isBalanceEnough(
        realBalance,
        this.props.newTransaction.amount
      );
      balance = toBN(realBalance)
        .div(toBN(10).pow(toBN(18)))
        .toFixed();
    } else {
      realBalance = await this.props.updateTokenBalance(this.state.token);
      balanceEnough = this.isBalanceEnough(
        realBalance,
        this.props.newTransaction.amount
      );
      balance = toBN(realBalance)
        .div(toBN(10).pow(toBN(App.ropstenTokenMap[this.state.token].decimals)))
        .toFixed();
    }
    console.log("balance: " + balance);
    this.setState({
      displayBalance: balance,
      realBalance: realBalance,
      balanceLoaded: true,
      balanceEnough: balanceEnough
    });
  }

  countDecimals = numStr => {
    if (
      Math.floor(parseFloat(numStr).valueOf()) === parseFloat(numStr).valueOf()
    )
      return 0;
    return numStr.split(".")[1].length || 0;
  };

  isValidAmount = amount => {
    if (amount === undefined || amount === "") {
      return false;
    }
    try {
      if (this.state.sendEther) {
        if (this.state.denom === "wei") {
          if (amount.includes(".")) {
            return false;
          }
        } else if (
          this.countDecimals(amount) >
          parseInt(NewTransaction.denomToPrecision[this.state.denom])
        ) {
          return false;
        }

        return true;
      } else {
        // Handle token precision (non-ether)
        let numDecimals = parseInt(
          App.ropstenTokenMap[this.state.token].decimals
        );
        return this.countDecimals(amount) <= numDecimals;
      }
    } catch (error) {
      return false;
    }
  };

  updateAmount = event => {
    this.props.updateNewTransaction("amount")(event);
    this.updateIsBalanceEnough(this.state.realBalance, event.target.value);
  };

  render() {
    const { classes } = this.props;
    let passphrase = this.state.code;

    const badAddr =
      this.props.newTransaction.recipient === undefined ||
      this.props.newTransaction.recipient === "" ||
      !window.web3.utils.isAddress(this.props.newTransaction.recipient);

    // TODO: need to check against number of decimals for the tokens.
    const validAmount = this.isValidAmount(this.props.newTransaction.amount);

    const badTimeInput = (() => {
      for (let key in this.props.newTransaction.holdTime) {
        if (this.isBadTimeInput(key)) {
          return true;
        }
      }
      return false;
    })();

    const disableSubmit = badAddr || !validAmount || badTimeInput;
    const handleFocus = event => event.target.select();

    return (
      <div>
        <Paper className={classes.root} elevation={1}>
          <Typography variant="h5" component="h3">
            New Payment
          </Typography>
          <Divider />
          <div>
            <Typography variant="h6" component={"h3"}>
              Recipient:
            </Typography>
            <TextField
              id="outlined-multiline-static-recipient"
              className={classes.textField}
              onChange={this.props.updateNewTransaction("recipient")}
              value={this.props.newTransaction.recipient}
              disabled={false}
              margin="normal"
              label={"Ethereum address"}
              variant="outlined"
              helperText={badAddr ? "Address is not valid" : ""}
              error={badAddr}
            />
          </div>
          <div>
            <Typography variant="h6" component={"h3"}>
              Amount to send:
            </Typography>
            <form className={classes.root} autoComplete="off">
              <FormControlLabel
                control={
                  <Switch
                    checked={!this.state.sendEther}
                    onChange={this.handleSwitch}
                    value="Send Tokens"
                  />
                }
                label="Send ERC20"
              />
              <div>
                <TextField
                  id="outlined-multiline-static-amount"
                  className={classes.textField}
                  disabled={false}
                  value={this.props.newTransaction.amount}
                  onChange={this.updateAmount}
                  margin="normal"
                  label={
                    this.state.sendEther ? this.state.denom : this.state.token
                  }
                  variant="outlined"
                  type={"number"}
                  helperText={
                    !validAmount
                      ? "Amount is not valid"
                      : !this.state.balanceLoaded
                      ? "Wait for balance to load"
                      : !this.state.balanceEnough
                      ? "Balance is not enough."
                      : ""
                  }
                  error={
                    !validAmount ||
                    !this.state.balanceLoaded ||
                    !this.state.balanceEnough
                  }
                />
                <FormControl className={classes.formControl}>
                  <InputLabel htmlFor="denom-helper">Denomination</InputLabel>
                  <Select
                    value={
                      this.state.sendEther ? this.state.denom : this.state.token
                    }
                    onChange={this.handleSelectChange}
                    input={
                      <Input
                        name={this.state.sendEther ? "denom" : "token"}
                        id={
                          this.state.sendEther ? "denom-helper" : "token-helper"
                        }
                      />
                    }
                  >
                    {this.state.sendEther
                      ? NewTransaction.selectedDenoms.map(key => {
                          return (
                            <MenuItem
                              selected={key === this.state.denom}
                              key={key}
                              value={key}
                            >
                              {key}
                            </MenuItem>
                          );
                        })
                      : NewTransaction.whitelistedTokens.map(key => {
                          return (
                            <MenuItem
                              selected={key === this.state.token}
                              key={key}
                              value={key}
                            >
                              {key}
                            </MenuItem>
                          );
                        })}
                  </Select>
                  <FormHelperText />
                </FormControl>
              </div>
            </form>
            <div>
              Your {this.state.sendEther ? "ETH" : this.state.token} balance is{" "}
              {this.state.displayBalance}.
            </div>
            <br />
          </div>
          <Typography variant="h6" component={"h3"}>
            Available time window:
          </Typography>
          The recipient can only retrieve the funds within this amount of time
          after the transaction is submitted.
          <div>
            {["days", "hours", "minutes"].map(input => {
              const badTime = this.isBadTimeInput(input);
              // See https://stackoverflow.com/questions/45939909/put-length-contraint-in-a-textfield-in-react-js
              return (
                <Fragment key={input}>
                  <TextField
                    id={"outlined-multiline-static-" + input}
                    value={this.props.newTransaction.holdTime[input]}
                    className={classes.textField}
                    disabled={false}
                    margin="normal"
                    label={input}
                    style={{ width: 100 }}
                    onChange={this.props.updateNewTransactionTime(
                      "holdTime",
                      input
                    )}
                    type={"number"}
                    variant={"outlined"}
                    error={badTime}
                    onFocus={handleFocus}
                    helperText={badTime ? "Integers only" : ""}
                    onInput={e => {
                      e.target.value = Math.max(0, parseInt(e.target.value))
                        .toString()
                        .slice(0, 2);
                    }}
                  />
                </Fragment>
              );
            })}
          </div>
          <br />
          <div>
            <Typography variant="h6" component="h4">
              Passphrase:{" "}
            </Typography>
            <TextField
              id="outlined-multiline-static-passphrase"
              value={passphrase.join(" ")}
              className={classes.textField}
              disabled={true}
              margin="normal"
              label={"Passphrase"}
              style={{ width: 300 }}
              variant={"outlined"}
              type={this.state.showPassword ? "text" : "password"}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton
                      aria-label="Toggle passphrase visibility"
                      onClick={this.handleClickShowPassword}
                    >
                      {this.state.showPassword ? (
                        <VisibilityOff />
                      ) : (
                        <Visibility />
                      )}
                    </IconButton>
                  </InputAdornment>
                )
              }}
            />
            <Tooltip title={"Generate a new code"}>
              <IconButton
                size="small"
                className={classes.button}
                variant="contained"
                color="secondary"
                onClick={this.refreshCode}
              >
                <RefreshIcon />
              </IconButton>
            </Tooltip>
            <CopyToClipboard
              text={passphrase.join(" ")}
              onCopy={() => this.setState({ copied: true })}
            >
              <Tooltip title={"Copy passphrase to clipboard"}>
                <IconButton>
                  <FileCopyIcon />
                </IconButton>
              </Tooltip>
            </CopyToClipboard>{" "}
            {this.state.copied ? (
              <span style={{ color: "red" }}>Copied.</span>
            ) : null}
          </div>
          <br />
          <Divider />
          <div>
            <Button
              className={classes.button}
              variant="contained"
              onClick={this.toggleSubmitDialog(true)}
              color="primary"
              disabled={disableSubmit}
              size="small"
            >
              Submit
            </Button>

            <Button
              className={classes.button}
              variant="contained"
              onClick={this.props.toggleNew(false)}
              size="small"
            >
              Cancel
            </Button>
            <SubmitDialog
              open={this.state.submitOpen}
              newTransaction={this.props.newTransaction}
              badAddr={badAddr}
              code={passphrase.join(" ")}
              toggleSubmit={this.toggleSubmitDialog}
              createTransaction={this.props.createTransaction}
              denom={this.state.denom}
              token={this.state.token}
              sendEther={this.state.sendEther}
              ethRefundContractInstance={this.props.ethRefundContractInstance}
            />
          </div>
          <br />
          {disableSubmit ? (
            <div>
              <span style={{ color: "red" }}>
                Please correct errors before submitting.
              </span>
            </div>
          ) : (
            ""
          )}
          <br />
          {
            <div>
              All transactions are charged a 0.001 ETH fee (separate from gas
              costs) to help maintain this service.
            </div>
          }
        </Paper>
      </div>
    );
  }
}

NewTransaction.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(NewTransaction);
